.App {
    text-align: center;
}

.mainmain {
    height: 100vh;
    background: #8c169b;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: antiquewhite;
    padding-top: 40px;
}

.imagenfondo {
    transition: transform 0.5s;
    margin-bottom: 30px;
}

.imagenfondo:hover {
    transform: scale(1.1);
    transition: transform 0.5s;
    cursor: pointer;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}
.tamaño-boton{
    margin-right: 10px;
}
@media (max-width: 500px) {
    .mainmain {
        width: 100%;
        height: 700px;
        background: #8c169b;
        display: flex;
        flex-direction: column;
        align-items: center;
        color: antiquewhite;
        padding-top: 40px;
        padding-bottom: 40px;
    }
    .boton-letra{
        font-size: 0.6em;
    }
    .tamaño-boton{
        width: 150px;
        margin: 0;
    }
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}